





























































































































import { Component, Vue } from 'vue-property-decorator';
import AmBtn from './components/AmBtn.vue';
import SideScroller from './components/SideScroller.vue';
interface Offsets {[key: string]: number; }

@Component({
    components: {
        AmBtn, SideScroller,
    },
})
export default class App extends Vue {
    public tops: number[] = [];
    public active: number = 0;
    public isDown: boolean = false;

    public created() {
        window.addEventListener('scroll', () => {
            if (window.scrollY > 100) {
                this.isDown = true;
            } else {
                this.isDown = false;
            }
            if (window.scrollY >= this.tops[0] - 100) {
                this.active = 0;
            }
            if (window.scrollY >= this.tops[1] - 100) {
                this.active = 1;
            }
            if (window.scrollY >= this.tops[2] - 100) {
                this.active = 2;
            }
            if (window.scrollY >= this.tops[3] - 100) {
                this.active = 3;
            }
        });
    }
    public mounted() {
        const elements = document.querySelectorAll('.am-scrollable-content');
        elements.forEach((element) => {
            this.tops.push(element.getBoundingClientRect().top + window.scrollY);
        });
    }
}
